import { css } from 'lit';

export const styles = css`:host {
  --header-background: var(--ig-expansion-panel-header-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --body-background: var(--ig-expansion-panel-body-background, hsla(var(--ig-surface-500), var(--ig-surface-a)));
  --header-focus-background: var(--ig-expansion-panel-header-focus-background, hsla(var(--ig-primary-500), 0.15));
  --header-title-color: var(--ig-expansion-panel-header-title-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --header-description-color: var(--ig-expansion-panel-header-description-color, hsla(var(--ig-gray-500), var(--ig-gray-a)));
  --header-icon-color: var(--ig-expansion-panel-header-icon-color, hsla(var(--ig-gray-600), var(--ig-gray-a)));
  --body-color: var(--ig-expansion-panel-body-color, hsla(var(--ig-gray-900), var(--ig-gray-a)));
  --disabled-color: var(--ig-expansion-panel-disabled-color, hsla(var(--ig-gray-400), var(--ig-gray-a)));
  --expanded-margin: var(--ig-expansion-panel-expanded-margin, 0.5rem);
  --border-radius: var(--ig-expansion-panel-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0.1875) * 1rem), 1rem));
  color: var(--body-color);
  background: var(--body-background);
  border-radius: var(--border-radius);
}

[part=header] {
  background: var(--header-background);
}
[part=header]:focus, [part=header]:active {
  background: var(--header-focus-background);
}

[part~=indicator] {
  color: var(--header-icon-color);
}

[part=title] {
  color: var(--header-title-color);
}

[part=subtitle] {
  color: var(--header-description-color);
}

:host([open]) [part~=content] {
  font-family: var(--ig-body-1-font-family, var(--ig-font-family));
  font-size: var(--ig-body-1-font-size);
  font-weight: var(--ig-body-1-font-weight);
  font-style: var(--ig-body-1-font-style);
  line-height: var(--ig-body-1-line-height);
  letter-spacing: var(--ig-body-1-letter-spacing);
  text-transform: var(--ig-body-1-text-transform);
  margin-top: var(--ig-body-1-margin-top);
  margin-bottom: var(--ig-body-1-margin-bottom);
}

:host([disabled]) [part~=indicator],
:host([disabled]) [part=title],
:host([disabled]) [part=subtitle] {
  color: var(--disabled-color);
}`;
